:root {
  /* --bg: #121212;
  --fg: #fff;
  --fg2: #fff;
  --fg3: #121212; */
  
  --bg: #fff;  
  --fg: #121212;
  --fg2: #121212; 
  --fg3: #fff;   
 
}
body{
  background: var(--bg);
}
.box{
  background: linear-gradient(45deg,#ff4971,#09f);
  position: relative;
  /* margin:10px; */
  padding: 0;
  margin-top: 18px;
  margin-left: 18px;
  width: calc(33.33vw - 24px);
  height: calc(33.33vw - 24px);
}

.link .link-underline{
  left: -200%;
  transition: all 0.3s ease-out 0.01s, background-color 1s ease-out;
}
.link:hover .link-underline{
  left: 0%;
}

.boxLink {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  width: fit-content;
  height: 2em;
  padding: 12px 24px;
  /* background: #fff; */
  color: var(--fg2);
  border: 5px solid var(--fg2);
  font-size: 1.3em;
  font-weight: 900;
  text-transform: uppercase;
}

.boxLink:hover {
  color: var(--fg3);
  background: var(--fg2);
  border: 5px solid var(--fg2);
}

.designBox {
  /* transform: translate(-50%,-50%); */
  /* position: absolute; */
  max-height: 40vh;
  max-width: 50vw;
  margin: 10px;
  /* cursor: pointer; */
}

.designDiv {
  width: calc( (100vw - 0px) / 4 );
  height: calc( (100vw - 0px) / 4 );
  margin-left: 0px;
  margin-top: 0px;
  /* overflow: hidden; */
  display: block;
  position: relative;
  /* background: #eee; */
}

.designDiv img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  opacity: 1;
}

